.main{
  display:flex;
  flex-direction: column;
  gap:50px;
}
.header{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap:120px;
  width:100%;
  height: 200px;
  background-color: powderblue;
  box-shadow: 0px 4px 2px 1px rgba(0, 0, 0, 0.2);
  font-size:15px;
  
}
.nameDiv{
  display:flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap:-50px;
}
.image{
  padding-top:20px ;
  padding-left:20px;
  width:100px;
  height: 100px;
}
.image img{
  width:200px;
  height: 150px;
}
.name{
font-size: 20px;
font-weight: bold;
}
.clg{
  font-size: 18px;
  font-weight: bold;
}
@media only screen and (max-width: 600px) {
  .heading {
       text-align: center;
  }
}